/* App.css */
.app {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 20px;
}

/* EmailSidebar.css */
.email-sidebar {
  width: 30%;
}

.email-list {
  overflow-y: auto;
}

.email-list-item {
  cursor: pointer;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

.email-list-item:hover {
  background-color: #f0f0f0;
}

.email-read {
  background-color: #f0f0f0;
}

.email-unread {
  background-color: #fff;
}

/* EmailBody.css */
.email-body {
  width: 60%;
}

.email-content {
  padding: 20px;
}

.email-placeholder {
  padding: 20px;
}
